import { Carousel, CarouselItem, Modal, MDBIcon } from "react-bootstrap";

const ImageItem = (props) => {
  return (
    <CarouselItem>
      <div className="itemContent">
        <img className="d-block w-100" src={props.href} alt="Logo" />
      </div>
    </CarouselItem>
  );
};

const directionButtons = (direction) => {
  var icon =
    direction === "Prev" ? (
      <span class="carousel-control-prev-icon"></span>
    ) : (
      <span class="carousel-control-next-icon"></span>
    );
  return (
    <div
      style={{
        backgroundColor: "black",
        width: 50,
        height: 50,
        borderRadius: "50%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {icon}
    </div>
  );
};

export const ProductModal = ({
  productModalVisible,
  productItem,
  hideModal,
}) => {
  var carouselItems = [];
  var image;

  if (productItem?.cardItem !== null && !productModalVisible) return null;

  let images = productItem.cardItem.images;

  if (images.length === 0)
    carouselItems[0] = ImageItem(productItem.cardItem.thumbnail);
  else
    carouselItems = productItem.cardItem.images.map((cardItem) =>
      ImageItem(cardItem)
    );
  console.log("carouselItems ", carouselItems);

  return (
    <Modal
      show={productModalVisible}
      onHide={hideModal}
      keyboard={false}
    >
      {/* <div
        style={{ fontSize: "4rem", right: 10, position: "absolute" }}
        aria-hidden="true"
      >
        Ã—
      </div> */}
      <Carousel
        nextIcon={directionButtons("Next")}
        prevIcon={directionButtons("Prev")}
        nextLabel={""}
        prevLabel={""}
      >
        {carouselItems}
      </Carousel>
    </Modal>
  );
};
